@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}

.header-h1 {
  font-size: 3rem;
}

.header-input {
  padding: 1rem;
  font-size: 1rem;
  width: 60%;
  outline: none;
}

.quotes {
  font-weight: bold;
}

.box-items {
  border: 1px solid #ccc;
  padding: 0.4rem;
  width: 25%;
}

.quotes-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 1rem;
}

.clicked-input {
  outline: 2px solid #00b5ff;
}

.wrapper {
  position: relative;
}

i.fa-search, i.fas {
  position: absolute;
  top: 20%;
  right: 19.5%;
  color: #4e4e4e;
  cursor: pointer;
}

i.fa-search:hover {
  color: #00b5ff;
}

.year-high {
  color: #25ff4e;
  font-weight: bolder;
}

.year-low {
  color: salmon;
  font-weight: bolder;
}

.blue-data {
  color: #2554ff;
  font-weight: bolder;
}
/*#ff8000*/

.countdown-div {
  position: absolute;
  width: 200px;
  left: 7%;
  top: 35%;
}

.h1-message {
  font-size: 1.2rem;
}
